.logoutWrapper {
  height: 100vh;
  background-color: whitesmoke;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  div {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
    width: 85%;
    height: 64px;
    background-color: white;
    border-radius: 6px;
    box-shadow: 0 10px 20px rgba(175, 159, 159, 0.19),
      0 6px 6px rgba(184, 163, 163, 0.23);
    a {
      width: 80%;
      color: black;
      text-decoration: none;
      i {
        display: inline-block;
        margin-right: 30px;
        width: 14%;
        &.fa-power-off {
          color: orangered;
        }
      }
    }
  }
}
