$mainBlue: rgb(0, 113, 167);
$orange: #d0826f;
$darkGrey: #3b4358;
$darkBlue: #263537;
$lightGrey: #dfe0e0;
$green: #228b22;
$seaGreen: #3cb371;
@mixin mainH1 {
  height: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  width: 100%;
  font-size: 17px;
  text-transform: uppercase;
  color: black;
  text-align: center;
  letter-spacing: 1px;
  box-shadow: 0 10px 20px rgba(175, 159, 159, 0.19);
}

@mixin defaultButton {
  margin: 0 auto;
  width: 80%;
  line-height: 58px;
  text-decoration: none;
  font-size: 22px;
  color: #111;
  background-color: white;
  margin-bottom: 44px;
  padding-left: 20px;
  box-shadow: inset 0 0 3px black;
  border-radius: 6px;
}

@mixin italicSpan {
  margin-top: 10px;
  text-transform: none;
  font-size: 12px;
  font-style: italic;
}

@mixin contentWrapper {
  background-color: whitesmoke;
  min-height: calc(100vh - 100px);
  padding: 20px 0px;
  p {
    box-sizing: border-box;
    background-color: #fff;
    margin-bottom: 20px;

    width: 95%;
    border-radius: 6px;
    margin: 0 auto;
    padding: 20px;
    span {
      display: block;
      font-weight: bold;
      margin-bottom: 10px;
    }
  }
  div.chart {
    p {
      text-align: left;
      box-shadow: none;
      width: 100%;
      padding: 0;
      font-weight: bold;
    }
    box-sizing: border-box;
    margin: 0 auto;
    margin-top: 20px;
    width: 95%;
    padding: 20px;
    background-color: #fff;
    border-radius: 6px;
  }
  div.buttons {
    border-radius: 6px;
    background-color: #fff;
    width: 95%;
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    margin-top: 20px;

    h2 {
      text-align: left;
      font-size: 16px;
      margin-bottom: 20px;
    }
    .singleButton {
      display: flex;
      margin-bottom: 20px;
      text-decoration: none;
      border: 2px solid silver;
      padding: 14px 20px;
      width: 160px;
      text-transform: capitalize;
      color: #111;
      background-color: whitesmoke;
      border-radius: 6px;
      box-shadow: inset 0 2px 2px rgba(184, 163, 163, 0.23);
      span {
        margin-right: 10px;
      }
      i {
        font-size: 20px;
        margin-right: 30px;
        &.fa-biohazard {
          color: darkcyan;
        }
        &.fa-vial {
          color: goldenrod;
        }
        &.fa-flask {
          color: purple;
        }
      }
    }
  }
}
