@import "./Variables.scss";

div.navigationWrapper {
  position: relative;
  div.burgerContainer {
    position: absolute;
    z-index: 2;
    top: 50px;
    left: 7%;
    transform: translateY(-50%);
    font-size: 28px;
    color: $darkBlue;
    &.open {
      position: fixed;
    }

    i.fa-bars.open {
      display: none;
    }
    i.fa-times {
      color: $darkBlue;

      &.close {
        display: none;
      }
    }
  }
  nav.navigation.close {
    transform: translateX(-100%);
  }

  nav.navigation {
    transition: 0.4s;
    position: fixed;
    z-index: 1;
    transform: translateX(0);
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    ul {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      li {
        list-style: none;
        width: 80%;
        text-align: left;
        margin-bottom: 36px;
        font-size: 22px;
        border-left: 4px solid grey;
        &:nth-child(4) {
          border-left: 4px solid transparent;
        }
        &:nth-child(4),
        &:nth-child(5),
        &:nth-child(6) {
          margin-left: 40px;
          font-size: 18px;
          border-left: 4px solid orangered;
        }
        a {
          text-decoration: none;
          color: #111;
          text-transform: uppercase;
          padding: 8px;
        }
      }
    }
  }
}
