.riskChartWrapper {
  margin-top: 20px;
  position: relative;
  width: 100%;
  height: auto;
  h4 {
    margin: 10px;
    margin-left: 0;
    &.desc {
      font-weight: normal;
    }
  }
  .riskWrap {
    background-color: whitesmoke;
    border-radius: 6px;
    box-shadow: inset 0 10px 20px rgba(145, 111, 111, 0.19),
      inset 0 6px 6px rgba(184, 163, 163, 0.23);
    .riskBar {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 50px;
      color: whitesmoke;
      box-shadow: inset 0 10px 20px rgba(145, 111, 111, 0.19),
        inset 0 6px 6px rgba(184, 163, 163, 0.23);
      border-top-left-radius: 6px;
      border-bottom-left-radius: 6px;
    }
  }
}
