@import url(https://fonts.googleapis.com/css2?family=Oxygen&display=swap);


*{margin:0;padding:0}html{font-family:"Oxygen", sans-serif}

div.loginPageWrapper{height:100vh;min-height:600px;background-image:url(/static/media/engine.0c22d2b0.jpg);background-size:cover}div.loginPageWrapper header.mainLogo{box-sizing:border-box;width:100%;padding:10px;padding-left:60px;background-color:#fff;text-align:left;color:#0071a7}div.loginPageWrapper header.mainLogo h1{text-transform:uppercase}div.loginPageWrapper header.mainLogo p{color:#263537}div.loginPageWrapper div.loginForm{border-radius:2px;margin:0 auto;background-color:rgba(255,255,255,0.3);padding:30px 0px 30px 0px;box-shadow:0 10px 20px rgba(175,159,159,0.19),0 6px 6px rgba(184,163,163,0.23);width:90%;margin-top:40%}div.loginPageWrapper div.loginForm form{display:flex;flex-direction:column;align-items:center;justify-content:center}div.loginPageWrapper div.loginForm form input{width:70%;height:54px;margin-bottom:30px;padding-left:20px;font-size:14px;border-radius:6px;border-style:none;background-color:#eee;box-shadow:inset 0 10px 20px rgba(175,159,159,0.19);color:black}div.loginPageWrapper div.loginForm form input:focus{border-style:none}div.loginPageWrapper div.loginForm form button.loginButton{width:35%;height:52px;background-color:#263537;border-radius:6px;border-style:none;color:white;text-transform:uppercase;box-shadow:0 10px 20px rgba(175,159,159,0.19),0 6px 6px rgba(184,163,163,0.23)}div.loginPageWrapper div.loginForm form button.loginButton a{color:white;text-decoration:none}

.homePageWrapper{min-height:calc(100vh);background-color:white}.homePageWrapper h1{height:100px;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:white;width:100%;font-size:17px;text-transform:uppercase;color:black;text-align:center;letter-spacing:1px;box-shadow:0 10px 20px rgba(175,159,159,0.19);margin-bottom:20px}.homePageWrapper .starterData{box-sizing:border-box;display:flex;flex-direction:column;width:95%;padding-top:20px;background-color:#fff;margin-bottom:20px;padding-left:20px;padding-right:20px;border-radius:4px;box-shadow:0 10px 20px rgba(175,159,159,0.19),0 6px 6px rgba(184,163,163,0.23);margin:0px auto 20px auto}.homePageWrapper .starterData span.hello{padding:10px;width:100%;font-size:20px;text-align:left}.homePageWrapper .starterData p.hello{padding-left:10px;width:100%;font-size:15px;text-align:left}.homePageWrapper .starterData div.infoElement{width:100%;margin-bottom:20px;position:relative}.homePageWrapper .starterData div.infoElement i{width:22px;text-align:center;font-size:22px;height:22px}.homePageWrapper .starterData div.infoElement i.fa-thermometer-three-quarters{color:orangered}.homePageWrapper .starterData div.infoElement i.fa-tint{color:royalblue}.homePageWrapper .starterData div.infoElement i.fa-cloud-showers-heavy{color:lightblue}.homePageWrapper .starterData div.infoElement i.fa-wind{color:#c0bfbf}.homePageWrapper .starterData div.infoElement i.fa-leaf{color:green}.homePageWrapper .starterData div.infoElement span{margin-left:20px}.homePageWrapper .starterData div.infoElement span:nth-child(3){position:absolute;right:0}.homePageWrapper .starterGraphs{display:flex;flex-direction:column;align-items:center;justify-content:center}.homePageWrapper .starterGraphs .singleElement{box-sizing:border-box;height:auto;width:95%;background-color:white;padding:20px;margin:10px auto;border-radius:4px;box-shadow:0 10px 20px rgba(175,159,159,0.19),0 6px 6px rgba(184,163,163,0.23)}.homePageWrapper .starterGraphs .singleElement p{box-sizing:border-box;margin-top:10px;width:100%;font-weight:bold;text-align:left;padding:0px 0px 10px 0px;border-bottom:2px solid green;color:black;text-transform:uppercase}.homePageWrapper .starterGraphs .singleElement a{margin:20px auto 0px auto;width:60%;line-height:32px;text-align:center;padding:6px;color:#111;border:2px solid silver;background-color:whitesmoke;display:block;border-radius:6px;text-decoration:none;box-shadow:inset 0 2px 2px rgba(184,163,163,0.23)}.homePageWrapper .starterGraphs .singleElement span.daysNumber{margin-top:10px;display:block;font-size:14px}

.userWrapper{position:relative}.userWrapper .logged{position:absolute;top:50px;right:7%;transform:translateY(-50%);font-size:24px}.userWrapper .logged a{color:silver;text-decoration:none}

div.navigationWrapper{position:relative}div.navigationWrapper div.burgerContainer{position:absolute;z-index:2;top:50px;left:7%;transform:translateY(-50%);font-size:28px;color:#263537}div.navigationWrapper div.burgerContainer.open{position:fixed}div.navigationWrapper div.burgerContainer i.fa-bars.open{display:none}div.navigationWrapper div.burgerContainer i.fa-times{color:#263537}div.navigationWrapper div.burgerContainer i.fa-times.close{display:none}div.navigationWrapper nav.navigation.close{transform:translateX(-100%)}div.navigationWrapper nav.navigation{transition:0.4s;position:fixed;z-index:1;transform:translateX(0);top:0;left:0;width:100%;height:100vh;display:flex;align-items:center;justify-content:center;background-color:white}div.navigationWrapper nav.navigation ul{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%}div.navigationWrapper nav.navigation ul li{list-style:none;width:80%;text-align:left;margin-bottom:36px;font-size:22px;border-left:4px solid grey}div.navigationWrapper nav.navigation ul li:nth-child(4){border-left:4px solid transparent}div.navigationWrapper nav.navigation ul li:nth-child(4),div.navigationWrapper nav.navigation ul li:nth-child(5),div.navigationWrapper nav.navigation ul li:nth-child(6){margin-left:40px;font-size:18px;border-left:4px solid orangered}div.navigationWrapper nav.navigation ul li a{text-decoration:none;color:#111;text-transform:uppercase;padding:8px}

.riskChartWrapper{margin-top:20px;position:relative;width:100%;height:auto}.riskChartWrapper h4{margin:10px;margin-left:0}.riskChartWrapper h4.desc{font-weight:normal}.riskChartWrapper .riskWrap{background-color:whitesmoke;border-radius:6px;box-shadow:inset 0 10px 20px rgba(145,111,111,0.19),inset 0 6px 6px rgba(184,163,163,0.23)}.riskChartWrapper .riskWrap .riskBar{position:relative;display:flex;align-items:center;justify-content:center;height:50px;color:whitesmoke;box-shadow:inset 0 10px 20px rgba(145,111,111,0.19),inset 0 6px 6px rgba(184,163,163,0.23);border-top-left-radius:6px;border-bottom-left-radius:6px}

.dataWrapper h1{height:100px;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:white;width:100%;font-size:17px;text-transform:uppercase;color:black;text-align:center;letter-spacing:1px;box-shadow:0 10px 20px rgba(175,159,159,0.19)}.dataWrapper .dataFromBase{background-color:whitesmoke}.dataWrapper .dataFromBase ul{display:flex;justify-content:center;width:100%;height:calc(100vh - 120px);background-color:#fff;padding:5px 0px 20px 0px}.dataWrapper .dataFromBase ul li{list-style:none;width:100%;margin:0 auto;margin-bottom:20px;box-sizing:border-box}.dataWrapper .dataFromBase ul li div{margin:0 auto;padding-bottom:10px;box-shadow:0 6px 6px rgba(184,163,163,0.23);margin-bottom:10px;width:95%;border-radius:6px}.dataWrapper .dataFromBase ul li div p{padding-top:4px;padding-bottom:4px;margin-bottom:4px;display:flex;justify-content:space-between}.dataWrapper .dataFromBase ul li div p.description{display:block;font-weight:bold;padding-left:30px;background-color:#ddd;border-top-right-radius:6px;border-top-left-radius:6px}.dataWrapper .dataFromBase ul li div p i{margin-right:10px}.dataWrapper .dataFromBase ul li div p span{margin-left:14px;margin-right:14px}

section.szara_plesn h1{height:100px;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:white;width:100%;font-size:17px;text-transform:uppercase;color:black;text-align:center;letter-spacing:1px;box-shadow:0 10px 20px rgba(175,159,159,0.19)}section.szara_plesn h1 span{margin-top:10px;text-transform:none;font-size:12px;font-style:italic}section.szara_plesn div.contentWrapper{background-color:whitesmoke;min-height:calc(100vh - 100px);padding:20px 0px}section.szara_plesn div.contentWrapper p{box-sizing:border-box;background-color:#fff;margin-bottom:20px;width:95%;border-radius:6px;margin:0 auto;padding:20px}section.szara_plesn div.contentWrapper p span{display:block;font-weight:bold;margin-bottom:10px}section.szara_plesn div.contentWrapper div.chart{box-sizing:border-box;margin:0 auto;margin-top:20px;width:95%;padding:20px;background-color:#fff;border-radius:6px}section.szara_plesn div.contentWrapper div.chart p{text-align:left;box-shadow:none;width:100%;padding:0;font-weight:bold}section.szara_plesn div.contentWrapper div.buttons{border-radius:6px;background-color:#fff;width:95%;padding-top:20px;padding-bottom:20px;display:flex;flex-direction:column;align-items:center;justify-content:center;margin:0 auto;margin-top:20px}section.szara_plesn div.contentWrapper div.buttons h2{text-align:left;font-size:16px;margin-bottom:20px}section.szara_plesn div.contentWrapper div.buttons .singleButton{display:flex;margin-bottom:20px;text-decoration:none;border:2px solid silver;padding:14px 20px;width:160px;text-transform:capitalize;color:#111;background-color:whitesmoke;border-radius:6px;box-shadow:inset 0 2px 2px rgba(184,163,163,0.23)}section.szara_plesn div.contentWrapper div.buttons .singleButton span{margin-right:10px}section.szara_plesn div.contentWrapper div.buttons .singleButton i{font-size:20px;margin-right:30px}section.szara_plesn div.contentWrapper div.buttons .singleButton i.fa-biohazard{color:darkcyan}section.szara_plesn div.contentWrapper div.buttons .singleButton i.fa-vial{color:goldenrod}section.szara_plesn div.contentWrapper div.buttons .singleButton i.fa-flask{color:purple}

.logoutWrapper{height:100vh;background-color:whitesmoke;display:flex;flex-direction:column;align-items:center;justify-content:center}.logoutWrapper div{display:flex;align-items:center;justify-content:center;margin-bottom:30px;width:85%;height:64px;background-color:white;border-radius:6px;box-shadow:0 10px 20px rgba(175,159,159,0.19),0 6px 6px rgba(184,163,163,0.23)}.logoutWrapper div a{width:80%;color:black;text-decoration:none}.logoutWrapper div a i{display:inline-block;margin-right:30px;width:14%}.logoutWrapper div a i.fa-power-off{color:orangered}

.userWrapper{background-color:whitesmoke}.userWrapper h1{height:100px;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:white;width:100%;font-size:17px;text-transform:uppercase;color:black;text-align:center;letter-spacing:1px;box-shadow:0 10px 20px rgba(175,159,159,0.19)}.userWrapper .userSection{padding-bottom:15px;background-color:#fff;width:95%;margin:20px auto;border-radius:6px}.userWrapper .userSection p{text-align:left;padding:10px 0px 10px 30px}.userWrapper .userSection p span{font-weight:bold;font-size:15px}.userWrapper .userSection .spec{margin:15px auto;text-align-last:left;display:block;padding:10px 0px 10px 30px;background-color:whitesmoke;width:250px;border-radius:6px;border:2px solid silver}.userWrapper .userSection .spec a{text-decoration:none;color:black}.userWrapper div.map iframe{width:100%;height:400px}

.confirmationWrapper{display:flex;align-items:center;justify-content:center;flex-direction:column;height:100vh;width:100%;z-index:2;background-color:whitesmoke}.confirmationWrapper .warning{width:95%;border-radius:6px;background-color:#fff;margin-bottom:20px;display:flex;flex-direction:column;box-shadow:0 4px 4px rgba(184,163,163,0.23)}.confirmationWrapper .warning p{font-size:18px;padding:10px 20px;text-align:center}.confirmationWrapper .warning p:nth-child(2){font-weight:bold}.confirmationWrapper .confirmationButtons{display:flex;flex-direction:row;justify-content:center}.confirmationWrapper .confirmationButtons .link{width:70px;line-height:26px;padding:10px;background-color:red;border-radius:6px;box-shadow:inset 0 10px 20px rgba(175,159,159,0.19),inset 0 6px 6px rgba(184,163,163,0.23)}.confirmationWrapper .confirmationButtons .link:nth-child(1){margin-right:60px;background-color:green}.confirmationWrapper .confirmationButtons .link a{text-align:center;display:block;text-decoration:none;color:white}

.chartWrapper{margin-bottom:30px}.chartWrapper span{display:flex;align-items:center;justify-content:center;height:60px;padding:5px 10px;text-align:center;background-color:whitesmoke;box-shadow:inset 0 10px 20px rgba(175,159,159,0.19),0 6px 6px rgba(184,163,163,0.23);margin-bottom:10px}.chartWrapper span:nth-child(2){font-weight:bold}

.statisticsWrapper{min-height:calc(100vh - 100px)}.statisticsWrapper h1{height:100px;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:white;width:100%;font-size:17px;text-transform:uppercase;color:black;text-align:center;letter-spacing:1px;box-shadow:0 10px 20px rgba(175,159,159,0.19)}.statisticsWrapper div.info{padding:10px;padding-left:20px}.statisticsWrapper div.info h2{font-size:18px;margin-bottom:10px}.statisticsWrapper div.info h2 i{margin-right:10px;color:blue}.statisticsWrapper div.info p{margin-bottom:10px}

