@import "./Variables.scss";
div.loginPageWrapper {
  height: 100vh;
  min-height: 600px;
  background-image: url("../img/engine.jpg");
  background-size: cover;
  header.mainLogo {
    box-sizing: border-box;
    width: 100%;
    padding: 10px;
    padding-left: 60px;
    background-color: #fff;
    text-align: left;
    color: $mainBlue;
    h1 {
      text-transform: uppercase;
    }
    p {
      color: $darkBlue;
    }
  }
  div.loginForm {
    border-radius: 2px;
    margin: 0 auto;
    background-color: rgba(255, 255, 255, 0.3);
    padding: 30px 0px 30px 0px;
    box-shadow: 0 10px 20px rgba(175, 159, 159, 0.19),
      0 6px 6px rgba(184, 163, 163, 0.23);
    width: 90%;
    margin-top: 40%;
    form {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      input {
        width: 70%;
        height: 54px;
        margin-bottom: 30px;
        padding-left: 20px;
        font-size: 14px;
        border-radius: 6px;
        border-style: none;
        background-color: #eee;
        box-shadow: inset 0 10px 20px rgba(175, 159, 159, 0.19);
        color: black;
        &:focus {
          border-style: none;
        }
      }
      button.loginButton {
        width: 35%;
        height: 52px;
        background-color: $darkBlue;
        border-radius: 6px;
        border-style: none;
        color: white;
        text-transform: uppercase;
        box-shadow: 0 10px 20px rgba(175, 159, 159, 0.19),
          0 6px 6px rgba(184, 163, 163, 0.23);
        a {
          color: white;
          text-decoration: none;
        }
      }
    }
  }
}
