.chartWrapper {
  margin-bottom: 30px;
  span {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    padding: 5px 10px;
    text-align: center;
    background-color: whitesmoke;
    box-shadow: inset 0 10px 20px rgba(175, 159, 159, 0.19),
      0 6px 6px rgba(184, 163, 163, 0.23);
    margin-bottom: 10px;
    &:nth-child(2) {
      font-weight: bold;
    }
  }
}
