@import "./Variables.scss";
.userWrapper {
  background-color: whitesmoke;
  h1 {
    @include mainH1;
  }
  .userSection {
    padding-bottom: 15px;
    background-color: #fff;
    width: 95%;
    margin: 20px auto;
    border-radius: 6px;
    p {
      text-align: left;
      padding: 10px 0px 10px 30px;
      span {
        font-weight: bold;
        font-size: 15px;
      }
    }
    .spec {
      margin: 15px auto;
      text-align-last: left;
      display: block;
      padding: 10px 0px 10px 30px;
      background-color: whitesmoke;
      width: 250px;
      border-radius: 6px;
      border: 2px solid silver;
      a {
        text-decoration: none;
        color: black;
      }
    }
  }
  div.map {
    iframe {
      width: 100%;
      height: 400px;
    }
  }
}
