@import "./Variables.scss";

.homePageWrapper {
  min-height: calc(100vh);
  background-color: white;
  h1 {
    @include mainH1;
    margin-bottom: 20px;
  }
  .starterData {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: 95%;
    padding-top: 20px;
    background-color: #fff;
    margin-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
    border-radius: 4px;
    box-shadow: 0 10px 20px rgba(175, 159, 159, 0.19),
      0 6px 6px rgba(184, 163, 163, 0.23);
    margin: 0px auto 20px auto;
    span.hello {
      padding: 10px;
      width: 100%;
      font-size: 20px;
      text-align: left;
    }
    p.hello {
      padding-left: 10px;
      width: 100%;
      font-size: 15px;
      text-align: left;
    }
    div.infoElement {
      width: 100%;
      margin-bottom: 20px;
      position: relative;
      i {
        width: 22px;
        text-align: center;
        font-size: 22px;
        height: 22px;
        &.fa-thermometer-three-quarters {
          color: orangered;
        }
        &.fa-tint {
          color: royalblue;
        }
        &.fa-cloud-showers-heavy {
          color: lightblue;
        }
        &.fa-wind {
          color: rgb(192, 191, 191);
        }
        &.fa-leaf {
          color: green;
        }
      }
      span {
        margin-left: 20px;
        &:nth-child(3) {
          position: absolute;
          right: 0;
        }
      }
    }
  }
  .starterGraphs {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .singleElement {
      box-sizing: border-box;
      height: auto;
      width: 95%;
      background-color: white;
      padding: 20px;
      margin: 10px auto;
      border-radius: 4px;
      box-shadow: 0 10px 20px rgba(175, 159, 159, 0.19),
        0 6px 6px rgba(184, 163, 163, 0.23);
      p {
        box-sizing: border-box;
        margin-top: 10px;
        width: 100%;
        font-weight: bold;
        text-align: left;
        padding: 0px 0px 10px 0px;
        border-bottom: 2px solid green;
        color: black;
        text-transform: uppercase;
      }
      a {
        margin: 20px auto 0px auto;
        width: 60%;
        line-height: 32px;
        text-align: center;
        padding: 6px;
        color: #111;
        border: 2px solid silver;
        background-color: whitesmoke;
        display: block;
        border-radius: 6px;
        text-decoration: none;
        box-shadow: inset 0 2px 2px rgba(184, 163, 163, 0.23);
      }
      span.daysNumber {
        margin-top: 10px;
        display: block;
        font-size: 14px;
      }
    }
  }
}
