.confirmationWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
  width: 100%;
  z-index: 2;
  background-color: whitesmoke;
  .warning {
    width: 95%;
    border-radius: 6px;
    background-color: #fff;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 4px rgba(184, 163, 163, 0.23);
    p {
      font-size: 18px;
      padding: 10px 20px;
      text-align: center;
      &:nth-child(2) {
        font-weight: bold;
      }
    }
  }
  .confirmationButtons {
    display: flex;
    flex-direction: row;
    justify-content: center;

    .link {
      width: 70px;
      line-height: 26px;
      padding: 10px;
      background-color: red;
      border-radius: 6px;
      box-shadow: inset 0 10px 20px rgba(175, 159, 159, 0.19),
        inset 0 6px 6px rgba(184, 163, 163, 0.23);
      &:nth-child(1) {
        margin-right: 60px;
        background-color: green;
      }
      a {
        text-align: center;
        display: block;
        text-decoration: none;
        color: white;
      }
    }
  }
}
