@import "./Variables.scss";

.dataWrapper {
  h1 {
    @include mainH1;
  }
  .dataFromBase {
    background-color: whitesmoke;

    ul {
      display: flex;
      justify-content: center;
      width: 100%;
      height: calc(100vh - 120px);
      background-color: #fff;
      padding: 5px 0px 20px 0px;

      li {
        list-style: none;
        width: 100%;
        margin: 0 auto;
        margin-bottom: 20px;
        box-sizing: border-box;
        div {
          margin: 0 auto;

          padding-bottom: 10px;
          box-shadow: 0 6px 6px rgba(184, 163, 163, 0.23);
          margin-bottom: 10px;
          width: 95%;
          border-radius: 6px;

          p {
            padding-top: 4px;
            padding-bottom: 4px;
            margin-bottom: 4px;
            display: flex;
            justify-content: space-between;
            &.description {
              display: block;
              font-weight: bold;
              padding-left: 30px;
              background-color: #ddd;
              border-top-right-radius: 6px;
              border-top-left-radius: 6px;
            }
            i {
              margin-right: 10px;
            }
            span {
              margin-left: 14px;
              margin-right: 14px;
            }
          }
        }
      }
    }
  }
}
